import { createSlice } from "@reduxjs/toolkit";

const initialState = {
    cartItem: []
};

const cartSlice = createSlice({
    name: 'cart',
    initialState,
    reducers:{
        add: (state,action)=>{
            state.cartItem.push(action.payload);
            // state.cartItem.pop();
        },
        delete: (state,action)=>{
                const index = state.cartItem.findIndex((x)=> x.id === action.payload);
                if(index===-1){
                    return;
                }
                if(state.cartItem[index].id===action.payload){
                state.cartItem.splice(index, 1);
                }
        }
    }
})

export const cartReducer = cartSlice.reducer;

export const cartactions = cartSlice.actions;

export const cartSelector = (state)=> state.cartReducer.cartItem;
