import { useParams } from 'react-router-dom'
import styles from './detail.module.css'
import { useSelector } from 'react-redux';
import { itemSelector } from '../../redux/reducers/itemReducer';
import { cartactions} from '../../redux/reducers/cartReducer';
import { useDispatch } from 'react-redux';


// Display the complete details of the item
export function Detail(){
    const params = useParams();
    const items = useSelector(itemSelector);
    const dispatch = useDispatch();
    const item = items.filter(item => item.id === parseInt(params.id));
    return (
        <div className={styles.detail}>
       <img src={item[0].image} alt={item[0].title} />
       <div className={styles.attributes}>
        <p><b>Title:</b> {item[0].title}</p>
        <p><b>Price:</b> ${item[0].price}</p>
        <p><b>Description:</b> {item[0].description}</p>
        <p><b>Category:</b> {item[0].category}</p>
        <p><b>Rating:</b> {item[0].rating.rate}</p>
        <p><b>Likes:</b> {item[0].rating.count}</p>
        <div className={styles.edit}>
            <img src="https://cdn.iconscout.com/icon/free/png-256/free-edit-mode-pencil-tool-change-30495.png" alt="edit"/>
            <button type="button" onClick={()=>dispatch(cartactions.add(item))}>Add to Cart</button>
            <button type="button" onClick={()=>dispatch(cartactions.delete(item.id))}>Delete</button>
            </div>
       </div>
        </div>
    )
}