import { useSelector } from "react-redux";
import { cartSelector } from "../../redux/reducers/cartReducer";
import styles from './cart.module.css';
import { cartactions } from "../../redux/reducers/cartReducer";
import { useDispatch } from "react-redux";


// Cart Page
export function Cart(){
    const cart = useSelector(cartSelector);
    const dispatch = useDispatch();
    let amount =0;
    
    // Calculate the total price
    function total(){
        cart.forEach(element => {
            amount +=element.price;
        });
        amount = Math.round(amount*100)/100;
    }
    total();

    // Cart Items
    return(
        <div>
        {cart.length?<div><ul className={styles.cart_item}>
        {cart.map((item)=><li className={styles.detail}><img src={item.image} alt={item.title}/>
        <div className={styles.attributes}>
        <p><b>Title:</b> {item.title}</p>
        <p><b>Description:</b> {item.description}</p>
        <p><b>Price:</b> ${item.price}</p>
        <div className={styles.edit}>
            <button type="button" onClick={()=>dispatch(cartactions.delete(item.id))}>Delete</button>
            </div>
       </div>
        </li>)}
        </ul>
        <div className={styles.total}><b>Total: ${amount}</b></div></div>:<p className={styles.message}>Your Cart is Empty!</p>}
        </div>
    )
}