import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import axios from "axios";


const initialState = {
    items: [],
};

export const getInitialStateAsync = createAsyncThunk('item/getInitialState',
()=>{
    try {
        return axios.get('https://my-json-server.typicode.com/anshkhandelwal-01/jsonItems/products');
    } catch (error) {
        console.log(error);
        return;
    }
}
);

export const sortByPrice = createAsyncThunk('item/sort',
()=>{
    try {
        return axios.get('https://my-json-server.typicode.com/anshkhandelwal-01/jsonItems/products');
    } catch (error) {
        console.log(error);
        return;
    }
}
);

export const addItemAsync = createAsyncThunk('item/addItem', async (payload)=>{
    try{
    const response = await fetch('https://my-json-server.typicode.com/anshkhandelwal-01/jsonItems/products',{
            method:"POST",
            headers: {
                "content-type": "application/json"
            },
            body:JSON.stringify(
                {   
                    "id": payload.id,
                    "title": payload.title,
                    "price": payload.price,
                    "description": payload.description,
                    "image": payload.image,
                    "category": payload.category
                }
            )
        });
           return response.json();
    } catch(error){
        console.log(error);
        return;
    }
}
);


const itemSlice = createSlice({
    name: 'item',
    initialState,
    reducers: {
        
    },
    extraReducers:(builder)=>{
        builder.addCase(getInitialStateAsync.fulfilled, (state,action)=>{
            state.items = [...action.payload.data];
        })
        .addCase(addItemAsync.fulfilled, (state,action)=>{
            state.items.push(action.payload);
        })
        .addCase(sortByPrice.fulfilled, (state,action)=>{
            let data = [];
            data = [...action.payload.data];
            state.items = data.sort((a,b)=> a.price - b.price);
        })
    }
})

export const itemReducer=itemSlice.reducer;

export const actions = itemSlice.actions;

export const itemSelector = (state)=>state.itemReducer.items;
