import styles from './create.module.css'
import { useDispatch } from 'react-redux';
import { addItemAsync } from '../../redux/reducers/itemReducer';
import { useState } from 'react';
import { useSelector } from 'react-redux';
import { notificationSelector, resetNotification } from '../../redux/reducers/notificationReducer';
import { cartactions } from '../../redux/reducers/cartReducer';

// Add new product
export function Create(){
  const dispatch = useDispatch();
  let [title, setTitle] = useState("");
  let [description, setDescription] = useState("");
  let [price, setPrice] = useState("");
  let [category, setCategory] = useState("");
  let [image, setImage] = useState("");
  const message = useSelector(notificationSelector);
  
  // Notification
  if(message){
    setTimeout(()=>{
      dispatch(resetNotification());
    }, 3000);
  }
  
  // Send the details of the new product to reducer
  function handleSubmit(e){
    e.preventDefault();
    let product = {
                  id: Date.now().toString(36),
                  title,
                  description,
                  category,
                  price,
                  image
                   }
    dispatch(cartactions.add())
    dispatch(addItemAsync(product));
    setTitle("");
    setDescription("");
    setPrice("");
    setCategory("");
    setImage("");
    }

    // Form for the fields of the new product
    return(
      <>
      {
        message && 
        <p class="alert alert-success" role="alert">
          {message}
        </p>
      }
        
    <form className={styles.create} onSubmit={handleSubmit}>
      <label>Title</label>
        <input type="text" name="title" placeholder="Enter Title" value={title} required onChange={(e)=>setTitle(e.target.value)}/>
        <label>Description</label>
        <input type="text" name="description" placeholder="Description" value={description} required onChange={(e)=>setDescription(e.target.value)}/>
        <label>Category</label>
        <input type="text" name="category" placeholder="Product Category" value={category} required onChange={(e)=>setCategory(e.target.value)}/>
        <label>Price</label>
        <input type="text" name="price" placeholder="Price in $" value={price} required onChange={(e)=>setPrice(e.target.value)}/>
        <label>Image</label>
        <input type="text" name="image" placeholder="Enter Image URL" value={image} required onChange={(e)=>setImage(e.target.value)}/>
        <button type="submit">
        Submit
        </button>
    </form>
    </>
    )
}