import {createSlice} from '@reduxjs/toolkit';
import { cartactions } from './cartReducer';
import { editActions } from './editReducer';

const initialState={
    message:""
}

const notificationSlice = createSlice({
    name:'notification',
    initialState,
    reducers:{
        reset: (state, action)=>{
            state.message="";
        }
    },
    extraReducers:{
         [cartactions.add]: (state, action)=>{
            state.message="Item is Added.";
        },
        [cartactions.delete]: (state, action)=>{
            state.message="Item is Deleted.";
        },
        [editActions.reset]: (state,action)=>{
            state.message="Description is Updated."
        }
    }
});

export const notificationReducer = notificationSlice.reducer;

export const resetNotification = notificationSlice.actions.reset;

export const notificationSelector = (state)=>state.notificationReducer.message;
