import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { getInitialStateAsync, itemSelector, sortByPrice } from "../../redux/reducers/itemReducer";
import { cartactions } from "../../redux/reducers/cartReducer";
import styles from './home.module.css';
// import { NavLink } from "react-router-dom";
import { resetNotification, notificationSelector } from "../../redux/reducers/notificationReducer";
import { editActions, editSelector } from "../../redux/reducers/editReducer";
import CarouselFunction from "../Carousel/Carousel";

export function Home(){

    const items = useSelector(itemSelector);
    let description = useSelector(editSelector);
    const dispatch = useDispatch();
    const message = useSelector(notificationSelector);
    let [sort, setSort] = useState(false);

    // Set the sorting
    function handleSort(value){
      setSort(value);
    }

    // Set the edited description
    function handChange(res){
      description = res;
    }
  
    // Notification
  if(message){
    setTimeout(()=>{
      dispatch(resetNotification());
    }, 3000);
  }

  // Set the State
  useEffect(()=>{
      if(sort){
        dispatch(sortByPrice());
      }else{
        dispatch(getInitialStateAsync());
      }
    },[dispatch, sort]);

    // Render the array of items fetched from the API
    return(
        <>
        {
        message && 
        <p className="alert alert-success" role="alert">
          {message}
        </p>
      }
      <button type="button" onClick={()=>handleSort(true)}>Sort by Price</button>&nbsp;{sort?<button onClick={()=>handleSort(false)} className={styles.cross}><b>X</b></button>:undefined}
      <CarouselFunction />
      <div className={styles.home}>
      {description?<div className={styles.editdetail}>
          <label>Description</label>
          <textarea onChange={(e)=>handChange(e.target.value)} defaultValue={description}/>
          <button onClick={()=>dispatch(editActions.reset())} type="button">Update</button>
        </div>:undefined}
        {items.map((item)=>
        <>
        <div className={styles.items} key={item.id}>
            <a href={`detail/${item.id}`}>
                <img className= {styles.list} src={item.image} alt={item.title} />
                </a>
            <div>
            <button type="button" onClick={()=>dispatch(editActions.set(item))}><img className={styles.edit} src="https://cdn.iconscout.com/icon/free/png-256/free-edit-mode-pencil-tool-change-30495.png" alt="edit"/></button>
            <button type="button" onClick={()=>dispatch(cartactions.add(item))}>Add to Cart</button>
            <button type="button" onClick={()=>dispatch(cartactions.delete(item.id))}>Delete</button>
            </div>
        </div>
        </>)}
        </div>
        </>
    )
}