import Button from 'react-bootstrap/Button';
import Container from 'react-bootstrap/Container';
import Form from 'react-bootstrap/Form';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { Outlet } from 'react-router-dom';
import styles from './navbar.module.css';
import { useSelector } from 'react-redux';
import { cartSelector } from '../../redux/reducers/cartReducer';

// Navbar using React Bootstrap
function NavBar() {
  const cart = useSelector(cartSelector);
  console.log(cart);
  return (
    <div className={styles.navbar}>
    <Navbar expand="lg" bg="dark" data-bs-theme="dark" className="bg-body-tertiary">
      <Container fluid>
        <Navbar.Toggle aria-controls="navbarScroll" />
        <Navbar.Collapse id="navbarScroll">
          <Nav
            className="me-auto my-2 my-lg-0"
            style={{ maxHeight: '100px' }}
            navbarScroll
          >
            <a href='/'>Home</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <a href='login'>Login</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <a href='signUp'>SignUp</a>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <a href='create'>Add a Product</a>
          </Nav>
          <Navbar.Brand style={{marginRight: '300px'}}>Shopzone</Navbar.Brand>
          <Form className="d-flex">
            <Form.Control
              type="search"
              placeholder="Search"
              className="me-2"
              aria-label="Search"
            />
            <Button variant="outline-success">Search</Button>
          </Form>&nbsp;&nbsp;
          <a href='cart'><img className={styles.cart} src='https://cdn-icons-png.flaticon.com/512/263/263142.png' alt='cart' /></a>
          <p>{cart.length}</p>
        </Navbar.Collapse>
      </Container>
    </Navbar>
    <Outlet />
    </div>
  );
}

export default NavBar;